body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.calculator {
  max-width: max-content;
  height: max-content;
  border: 4px solid black;
  margin: auto;
  display: grid;
  grid-template-areas: 'display display' 'upper-option upper-option' 'number side-option';
  grid-template-rows: 0fr 0fr 0fr;
}

.display {
  width: 100%;
  height: 50px;
  background-color: rgb(240, 240, 240);
  text-align: right;
  grid-area: display;
  font-size: 36px;
  margin-bottom: 10px;
}

.upper-option {
  display: grid;
  margin-bottom: 3px;
  grid-area: upper-option;
  gap: 3px;
  grid-template-areas: 'clear delete persen divided';
}

.upper-option button {
  width: 80px;
  height: 40px;
  border-style: none;
  font-size: 20px;
}

.side-option {
  width: 80px;
  grid-area: side-option;
  display: grid;
  gap: 3px;
}

.side-option button {
  width: 80px;
  height: 40px;
  border-style: none;
  font-size: 20px;
}

.number {
  margin-right: 3px;
  grid-area: number;
  display: grid;
  grid-template-areas: 'seven eight nine' 'four five six' 'one two three';
  gap: 3px;
}

.number button {
  width: 80px;
  height: 40px;
  border-style: none;
  font-size: 20px;
}